:where(html) {
  /**
  * @tokens Animations
  * @presenter Animation
  */

  --ctek-global-animation-fade-in: fade-in 0.5s var(--ctek-global-ease-3);
  --ctek-global-animation-fade-out: fade-out 0.5s var(--ctek-global-ease-3);
  --ctek-global-animation-scale-up: scale-up 0.5s var(--ctek-global-ease-3);
  --ctek-global-animation-scale-down: scale-down 0.5s var(--ctek-global-ease-3);
  --ctek-global-animation-slide-out-up: slide-out-up 0.5s var(--ctek-global-ease-3);
  --ctek-global-animation-slide-out-down: slide-out-down 0.5s var(--ctek-global-ease-3);
  --ctek-global-animation-slide-out-right: slide-out-right 0.5s var(--ctek-global-ease-3);
  --ctek-global-animation-slide-out-left: slide-out-left 0.5s var(--ctek-global-ease-3);
  --ctek-global-animation-slide-in-up: slide-in-up 0.5s var(--ctek-global-ease-3);
  --ctek-global-animation-slide-in-down: slide-in-down 0.5s var(--ctek-global-ease-3);
  --ctek-global-animation-slide-in-right: slide-in-right 0.5s var(--ctek-global-ease-3);
  --ctek-global-animation-slide-in-left: slide-in-left 0.5s var(--ctek-global-ease-3);
  --ctek-global-animation-shake-x: shake-x 0.75s var(--ctek-global-ease-out-5);
  --ctek-global-animation-shake-y: shake-y 0.75s var(--ctek-global-ease-out-5);
  --ctek-global-animation-spin: spin 2s linear infinite;
  --ctek-global-animation-ping: ping 5s var(--ctek-global-ease-out-3) infinite;
  --ctek-global-animation-blink: blink 1s var(--ctek-global-ease-out-3) infinite;
  --ctek-global-animation-float: float 3s var(--ctek-global-ease-in-out-3) infinite;
  --ctek-global-animation-bounce: bounce 2s var(--ctek-global-ease-squish-2) infinite;
  --ctek-global-animation-pulse: pulse 2s var(--ctek-global-ease-out-3) infinite;

  /**
    * @tokens-end
    */
}

@keyframes fade-in {

  to {
    opacity: 1;
  }
}

@keyframes fade-out {

  to {
    opacity: 0;
  }
}

@keyframes scale-up {

  to {
    transform: scale(1.25);
  }
}

@keyframes scale-down {

  to {
    transform: scale(0.75);
  }
}

@keyframes slide-out-up {

  to {
    transform: translateY(-100%);
  }
}

@keyframes slide-out-down {

  to {
    transform: translateY(100%);
  }
}

@keyframes slide-out-right {

  to {
    transform: translateX(100%);
  }
}

@keyframes slide-out-left {

  to {
    transform: translateX(-100%);
  }
}

@keyframes slide-in-up {

  from {
    transform: translateY(100%);
  }
}

@keyframes slide-in-down {

  from {
    transform: translateY(-100%);
  }
}

@keyframes slide-in-right {

  from {
    transform: translateX(-100%);
  }
}

@keyframes slide-in-left {

  from {
    transform: translateX(100%);
  }
}

@keyframes shake-x {

  0%,
  100% {
    transform: translateX(0%);
  }

  20% {
    transform: translateX(-5%);
  }

  40% {
    transform: translateX(5%);
  }

  60% {
    transform: translateX(-5%);
  }

  80% {
    transform: translateX(5%);
  }
}

@keyframes shake-y {

  0%,
  100% {
    transform: translateY(0%);
  }

  20% {
    transform: translateY(-5%);
  }

  40% {
    transform: translateY(5%);
  }

  60% {
    transform: translateY(-5%);
  }

  80% {
    transform: translateY(5%);
  }
}

@keyframes spin {

  to {
    transform: rotate(1turn);
  }
}

@keyframes ping {

  90%,
  100% {
    transform: scale(2);
    opacity: 0;
  }
}

@keyframes blink {

  0%,
  100% {
    opacity: 1;
  }

  50% {
    opacity: 0.5;
  }
}

@keyframes float {

  50% {
    transform: translateY(-25%);
  }
}

@keyframes bounce {

  25% {
    transform: translateY(-20%);
  }

  40% {
    transform: translateY(-3%);
  }

  0%,
  60%,
  100% {
    transform: translateY(0);
  }
}

@keyframes pulse {

  50% {
    transform: scale(0.9, 0.9);
  }
}
