@use '../init' as *;

:where(blockquote) {
  display: grid;
  gap: var(--ctek-global-space-sm);
  margin: 0 0 var(--ctek-global-space-lg);
  font-size: 18px;
  border-inline-start-width: 3px;
  padding-block: var(--ctek-global-space-md);
  padding-inline: var(--ctek-global-space-md);

  * + & {
    margin-top: var(--ctek-global-space-lg);
  }

  p {
    color: inherit;
    font-weight: inherit;
    font-size: inherit;
    font-family: inherit;
    line-height: inherit;
  }

  @media print {
    page-break-inside: avoid;
  }
}

:where(:not(blockquote) > cite) {
  padding-inline-start: var(--size-2);
}
