:where(html) {
  /**
    * @tokens Borders
    * @presenter Border
    */
  --ctek-global-border-size-1: 1px;
  --ctek-global-border-size-2: 2px;
  --ctek-global-border-size-3: 5px;
  --ctek-global-border-size-4: 10px;
  --ctek-global-border-size-5: 25px;

  /**
    * @tokens Border Radius
    * @presenter BorderRadius
    */
  --ctek-global-radius-1: 2px;
  --ctek-global-radius-2: 5px;
  --ctek-global-radius-3: 1rem;
  --ctek-global-radius-4: 2rem;
  --ctek-global-radius-5: 4rem;
  --ctek-global-radius-6: 8rem;
  --ctek-global-radius-round: 1e5px;
  --ctek-global-radius-conditional-1: clamp(0px, calc(100vw - 100%) * 1e5, var(--radius-1));
  --ctek-global-radius-conditional-2: clamp(0px, calc(100vw - 100%) * 1e5, var(--radius-2));
  --ctek-global-radius-conditional-3: clamp(0px, calc(100vw - 100%) * 1e5, var(--radius-3));
  --ctek-global-radius-conditional-4: clamp(0px, calc(100vw - 100%) * 1e5, var(--radius-4));
  --ctek-global-radius-conditional-5: clamp(0px, calc(100vw - 100%) * 1e5, var(--radius-5));
  --ctek-global-radius-conditional-6: clamp(0px, calc(100vw - 100%) * 1e5, var(--radius-6));

  /**
    * @tokens-end
    */
}
