@use '../init' as *;

:where(html) {
  /**
    * @tokens Spacings
    * @presenter Spacing
  */

  --ctek-global-space-xs: 0.063rem; // 1px
  --ctek-global-space-sm: 0.125rem; // 2px
  --ctek-global-space-05: 0.25rem; // 4px
  --ctek-global-space-1: 0.5rem; // 8px
  --ctek-global-space-105: 0.75rem; // 12px
  --ctek-global-space-2: 1rem; // 16px
  --ctek-global-space-205: 1.25rem; // 20px
  --ctek-global-space-3: 1.5rem; // 24px
  --ctek-global-space-4: 2rem; // 32px
  --ctek-global-space-5: 2.5rem; // 40px
  --ctek-global-space-6: 3rem; // 48px
  --ctek-global-space-7: 3.5rem; // 56px
  --ctek-global-space-8: 4rem; // 64px
  --ctek-global-space-9: 4.5rem; // 72px
  --ctek-global-space-10: 5rem; // 80px
  --ctek-global-space-11: 5.5rem; // 88px
  --ctek-global-space-12: 6rem; // 96px
  --ctek-global-space-13: 6.5rem; // 104px
  --ctek-global-space-14: 7rem; // 112px
  --ctek-global-space-15: 7.5rem; // 120px
  --ctek-global-space-20: 10rem; // 160px
  --ctek-global-space-site: var(--ctek-global-space-2); // 16px
  --ctek-global-max-width-sm: 800px;
  --ctek-global-max-width-md: 1100px;
  --ctek-global-max-width-full: 100%;

  /**
    * @tokens Responsive Spacing
    * @presenter Spacing
  */

  --ctek-global-space-5-7-11: var(--ctek-global-space-5);
  --ctek-global-space-7-11: var(--ctek-global-space-7);

  @include bp(md) {
    --ctek-global-space-5-7-11: var(--ctek-global-space-7);
    --ctek-global-space-7-11: var(--ctek-global-space-11); // 56px mobile/tablet, 88px desktop
  }

  @include bp(lg) {
    --ctek-global-space-5-7-11: var(--ctek-global-space-11); // 40px mobile, 56px tablet, 88px desktop
  }

  /**
    * @tokens-end
  */
}
