@use '../init' as *;

:where(html) {
  /**
  * @tokens Fonts
  * @presenter FontFamily
  */

  --ctek-global-font-sans: helvetica, sans-serif;
  --ctek-global-font-serif: georgia, 'Times New Roman', times, serif;
  --ctek-global-font-system: -apple-system, blinkmacsystemfont, 'Segoe UI', roboto, 'Oxygen-Sans', ubuntu, cantarell, 'Fira Sans', 'Droid Sans', sans-serif;
  --ctek-global-font-mono: menlo, consolas, 'Lucida Console', 'Liberation Mono', 'Courier New', monospace, sans-serif;
  --ctek-global-font-local: 'Source Sans Pro', sans-serif;
  --ctek-global-ff-primary: var(--ctek-global-font-serif);
  --ctek-global-ff-secondary: var(--ctek-global-font-local);
  --ctek-global-ff-mono: var(--ctek-global-font-mono);
  --ctek-global-ff-local: var(--ctek-global-font-local);

  /**
  * @tokens Font Sizes
  * @presenter FontSize
  */

  --ctek-global-fs-000: 0.625rem; // 10px
  --ctek-global-fs-00: 0.75rem; // 12px
  --ctek-global-fs-0: 0.875rem; // 14px
  --ctek-global-fs-1: 1rem; // 16px
  --ctek-global-fs-2: 1.125rem; // 18px
  --ctek-global-fs-3: 1.25rem; // 20px
  --ctek-global-fs-4: 1.375rem; // 22px
  --ctek-global-fs-5: 1.5rem; // 24px
  --ctek-global-fs-6: 1.75rem; // 28px
  --ctek-global-fs-7: 2rem; // 32px
  --ctek-global-fs-8: 2.25rem; // 36px
  --ctek-global-fs-9: 2.5rem; // 40px
  --ctek-global-fs-10: 3rem; // 48px
  --ctek-global-fs-11: 3.5rem; // 56px
  --ctek-global-fs-12: 4rem; // 64px
  --ctek-global-fs-13: 5rem; // 80px
  --ctek-global-fs-14: 7.5rem; // 120px

  // --ctek-global-fs-scale-ratio: 1.2;

  // // /* Don't change the lines below, you only need to change the --ctek-global-fs-scale-ratio above. */
  // --ctek-global-fs-lg-1: calc(1rem * var(--ctek-global-fs-scale-ratio));
  // --ctek-global-fs-lg-2: calc(var(--ctek-global-fs-lg-1) * var(--ctek-global-fs-scale-ratio));
  // --ctek-global-fs-lg-3: calc(var(--ctek-global-fs-lg-2) * var(--ctek-global-fs-scale-ratio));
  // --ctek-global-fs-lg-4: calc(var(--ctek-global-fs-lg-3) * var(--ctek-global-fs-scale-ratio));
  // --ctek-global-fs-lg-5: calc(var(--ctek-global-fs-lg-4) * var(--ctek-global-fs-scale-ratio));
  // --ctek-global-fs-lg-6: calc(var(--ctek-global-fs-lg-5) * var(--ctek-global-fs-scale-ratio));
  // --ctek-global-fs-lg-7: calc(var(--ctek-global-fs-lg-6) * var(--ctek-global-fs-scale-ratio));
  // --ctek-global-fs-sm-1: calc(1rem / var(--ctek-global-fs-scale-ratio));
  // --ctek-global-fs-sm-2: calc(var(--ctek-global-fs-sm-1) / var(--ctek-global-fs-scale-ratio));
  // --ctek-global-fs-sm-3: calc(var(--ctek-global-fs-sm-2) / var(--ctek-global-fs-scale-ratio));

  // https: //www.fluid-type-scale.com/calculate?minFontSize=16&minWidth=400&minRatio=1.1&maxFontSize=19&maxWidth=1280&maxRatio=1.2&steps=sm%2Cbase%2Cmd%2Clg%2Cxl%2Cxxl%2Cxxxl&baseStep=base&prefix=ctek-global-fs&decimals=2&useRems=on&remValue=16&previewFont=Inter
  // --ctek-global-fs-sm: clamp(0.91rem, 0.15vw + 0.87rem, 0.99rem);
  // --ctek-global-fs-base: clamp(1rem, 0.34vw + 0.91rem, 1.19rem);
  // --ctek-global-fs-md: clamp(1.1rem, 0.59vw + 0.95rem, 1.43rem);
  // --ctek-global-fs-lg: clamp(1.21rem, 0.91vw + 0.98rem, 1.71rem);
  // --ctek-global-fs-xl: clamp(1.33rem, 1.31vw + 1rem, 2.05rem);
  // --ctek-global-fs-xxl: clamp(1.46rem, 1.82vw + 1.01rem, 2.46rem);
  // --ctek-global-fs-xxxl: clamp(1.61rem, 2.44vw + 1rem, 2.95rem);

  /**
  * @tokens Font Weights
  * @presenter FontWeight
  */
  --ctek-global-fw-thin: 100;
  --ctek-global-fw-xlight: 200;
  --ctek-global-fw-light: 300;
  --ctek-global-fw-regular: 400;
  --ctek-global-fw-medium: 500;
  --ctek-global-fw-semibold: 600;
  --ctek-global-fw-bold: 700;
  --ctek-global-fw-xbold: 800;

  /**
  * @tokens Line Heights
  * @presenter LineHeight
  */
  --ctek-global-lh-none: 1;
  --ctek-global-lh-sm: 1.2;
  --ctek-global-lh-md: 1.5;

  /**
      * @tokens Letter Spacings
      * @presenter LetterSpacing
      */

  --ctek-global-ls-0: -0.05em;
  --ctek-global-ls-1: 0.025em;
  --ctek-global-ls-2: 0.05em;
  --ctek-global-ls-3: 0.75em;
  --ctek-global-ls-4: 0.15em;
  --ctek-global-ls-5: 0.5em;
  --ctek-global-ls-6: 0.75em;
  --ctek-global-ls-7: 1em;

  /**
    * @tokens-end
    */
}
