@use '../init' as *;

h1,
h2,
h3,
h4,
h5,
h6 {
  width: auto;
  margin: 0 0 0.5em;
  color: var(--ctek-global-color-text);
  font-weight: var(--ctek-global-fw-bold);
  font-family: var(--ctek-global-ff-secondary);
  font-style: normal;
  line-height: var(--ctek-global-lh-none);
  overflow-wrap: break-word;

  & + & {
    margin-top: 0;
  }

  p + & {
    margin-top: 1em;
  }
}
