@use '../init' as *;

:where(body) {
  min-height: 100vh;
  margin: 0;
  padding: 0;
  font-weight: var(--ctek-global-fw-regular);
  font-family: var(--ctek-global-ff-primary);
  line-height: var(--ctek-global-lh-md);
  word-wrap: break-word;
  min-block-size: 100%;
  scrollbar-gutter: stable both-edges;
  text-rendering: optimizespeed;
}
