@use '../init' as *;

:where(img) {
  block-size: auto;
  display: block;
  max-inline-size: 100%;

  @media print {
    max-width: 100% !important;
    page-break-inside: avoid;
  }
}

:where(picture) {
  block-size: auto;
  display: block;
  max-inline-size: 100%;

  @media print {
    max-width: 100% !important;
    page-break-inside: avoid;
  }
}
