:where(html) {
  /**
  * @tokens Functional Colors
  * @presenter Color
  */
  --ctek-global-color-primary: var(--ctek-global-color-blue-400);
  --ctek-global-color-primary-lighter: var(--ctek-global-color-blue-200);
  --ctek-global-color-primary-darker: var(--ctek-global-color-blue-600);
  --ctek-global-color-secondary: var(--ctek-global-color-orange-400);
  --ctek-global-color-secondary-lighter: var(--ctek-global-color-orange-200);
  --ctek-global-color-secondary-darker: var(--ctek-global-color-orange-600);
  --ctek-global-color-text: var(--ctek-global-color-black);

  /**
    * @tokens Gray
    * @presenter Color
    */
  --ctek-global-color-white: hsl(0deg 0% 100%);
  --ctek-global-color-gray-50: hsl(0deg 0% 97%);
  --ctek-global-color-gray-100: hsl(0deg 0% 94%);
  --ctek-global-color-gray-200: hsl(0deg 0% 89%);
  --ctek-global-color-gray-300: hsl(0deg 0% 82%);
  --ctek-global-color-gray-400: hsl(0deg 0% 66%);
  --ctek-global-color-gray-500: hsl(0deg 0% 55%);
  --ctek-global-color-gray-600: hsl(0deg 0% 46%);
  --ctek-global-color-gray-700: hsl(0deg 0% 39%);
  --ctek-global-color-gray-800: hsl(0deg 0% 31%);
  --ctek-global-color-gray-900: hsl(0deg 0% 25%);
  --ctek-global-color-gray-1000: hsl(0deg 0% 17%);
  --ctek-global-color-gray-1100: hsl(0deg 0% 11%);
  --ctek-global-color-black: hsl(0deg 0% 0%);

  /**
      * @tokens Blue
      * @presenter Color
      */

  --ctek-global-color-blue-200: hsl(214deg 69% 81%);
  --ctek-global-color-blue-400: hsl(214deg 72% 31%);
  --ctek-global-color-blue-600: hsl(214deg 72% 28%);

  /**
        * @tokens Orange
        * @presenter Color
        */
  --ctek-global-color-orange-200: hsl(43deg 95% 62%);
  --ctek-global-color-orange-400: hsl(43deg 100% 49%);
  --ctek-global-color-orange-600: hsl(43deg 97% 44%);

  /**
      * @tokens Status
      * @presenter Color
      */
  --ctek-global-color-alert-warning: #f9fb93;
  --ctek-global-color-alert-error: #fbe3e4;
  --ctek-global-color-alert-message: #cfefc2;
}

/**
  * @tokens-end
  */
